body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header_bar{
    top:0px;
    left: 0px;
    position: absolute;
    width: 100%;
    background-color: darkgreen;
    color: #eeffee;
    font-size: 24px;
    padding: 15px;
}

.content_area{
    margin-top: 70px;
}

.custom_button{
    width: 300px;
    padding:10px;
    background-color: darkgreen;
    color: #eeffee;
    text-align: center;
    margin-bottom: 50px;
    display: inline-block;
}

.custom_button:hover{
    background-color: #339933;
}

.custom_button:active{
    background-color: #225522;
}

.link_ico{
    display: inline-block;
    margin-top: -50px;
}
.question_block{
    padding: 15px;
    font-size: 20px;
    color: #555555
}

.key_block{
    padding: 10px;
    font-size: 18px;
    color: #555555
}

.key_block:hover{
    background-color: #eeffee;
}

.key_block:active{
    background-color: #aaffaa;
}


.wrong_block{
    padding: 10px;
    font-size: 18px;
    color: #999999;
    background-color: #aa5555;
}

.correct_block{
    padding: 10px;
    font-size: 18px;
    color: #333333;
    background-color: #55aa55;
}


